<script>
import { inject, onMounted, onUnmounted, reactive, ref } from "vue";
import { useDialog } from "@/composable/semen";

import SemenCreateEditDailog from "@/components/semen/create-edit-dialog.vue";
import { emitter } from "@/event-bus";

export default {
    components: {
        SemenCreateEditDailog
    },
    setup() {
        const service = inject("semenService");
        const table = reactive([]);
        const selectedTable = reactive([]);

        const { dialog, create, edit, close } = useDialog();

        onMounted(() => {
            table.push(...service.getTable());
            emitter.on("semen-create-edit-dialog:close", close);
        });

        onUnmounted(() => {
            emitter.off("semen-create-edit-dialog:close", close);
        });

        return {
            table,
            selectedTable,
            dialog,
            create,
            edit,
            close
        };
    }
};
</script>
