import { reactive } from "vue";

export function useDialog() {
    const dialog = reactive({
        type: "Create",
        display: false
    });

    const create = () => {
        dialog.display = true;
        dialog.type = "Create";
    };

    const edit = () => {
        dialog.display = true;
        dialog.type = "Edit";
    };

    const close = () => {
        dialog.display = false;
        dialog.type = "Create";
    };

    return { dialog, create, edit, close };
}
